/* Caixa de mensagem exibindo instrues de uso ao usurio. */

import React from "react";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogActions from '@mui/material/DialogActions';

export class TutorialDialog extends React.Component {
  render() {
  	const onClose = this.props.onClose;
  	const open = this.props.open;
  	
    return (
    	<Dialog
    	 onClose={onClose}
    	 open={open}
    	 aria-labelledby="tutorial-title">
    		<DialogTitle id="tutorial-title">
    			Tutorial de uso da aplicao
    		</DialogTitle>
    		
    		<DialogContent>
    			<DialogContentText>
    				Bem-vindo! Esta aplicao exibe as informaes das
    				bibliotecas universitrias do Brasil atravs de um
    				mapa interativo. Caso queira ler este tutorial
    				novamente, clique no boto de ajuda ao lado.
    				
    				<br /> <br />
    				A viso inicial do mapa exibe as 5 regies do
    				Brasil com a quantidade de instituies em cada
    				uma delas. Ao clicar no marcador de uma regio, voc v
    				uma viso semelhante, mas em relao aos estados
    				da regio. Voc pode retornar  viso anterior
    				clicando no boto "Retornar  viso geral".
    				
    				<br /> <br />
    				Ao clicar em um estado, voc v os marcadores
    				das instituies. Marcadores muito prximos ficam
    				agrupados; voc pode clicar nos marcadores de grupo
    				ou navegar usando o zoom (atravs do scroll do mouse
    				ou os botes de diminuir e aumentar zoom no menu
    				inferior). Clicando em um marcador de instituio,
    				voc tem acesso a um resumo das informaes da
    				biblioteca e pode ir  pgina da instituio para
    				ver mais detalhes ou obter direes  instituio.
    				
    				<br /> <br />
    				Clicando no boto "Opes de busca", voc pode
    				filtrar as instituies de acordo com suas
    				caractersticas. Aps selecionar as opes
    				desejadas, clique em "Buscar" para confirmar,
    				"Limpar" para reiniciar os filtros e "Fechar"
    				para sair. O boto "Limpar busca", ao lado do
    				boto de "Opes de busca", remove os filtros
    				de busca.
    				
    				<br /> <br />
    				O menu inferior exibe opes de controle de zoom,
    				brilho, contraste e esquema de cores do mapa.
    				O boto "Voltar aos valores originais" reinicia
    				o zoom, retornando  viso inicial, bem como os
    				valores de brilho, contraste e esquema de cores.
    				
    				<br /> <br />
    				Usurios de leitores de tela podem
    				navegar pelo mapa usando as teclas Tab (para avanar
    				a seleo), Enter (para confirmar a seleo),
    				Shift + Tab (para retornar a seleo) e Esc (para
    				fechar menus).
    			</DialogContentText>
    		</DialogContent>
    		
    		<DialogActions>
    			<Button onClick={onClose}>Fechar</Button>
    		</DialogActions>
    	</Dialog>
    );
  }
}
