/* Pgina mostrando as informaes de uma IES em detalhes. */

import React from "react";
import { useParams, useNavigate } from "react-router-dom";
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import Chip from '@mui/material/Chip';
import Grid from '@mui/material/Grid';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

import "./style.css";

const ORG_ACADEMICA = {
	'1': 'Universidade',
	'2': 'Centro Universitrio',
	'3': 'Faculdade',
	'4': 'Instituto Federal de Educao, Cincia e Tecnologia',
	'5': 'Centro Federal de Educao Tecnolgica'
};

const CATEGORIA_ADMIN = {
	'1': 'Pblica Federal',
	'2': 'Pblica Estadual',
	'3': 'Pblica Municipal',
	'4': 'Privada com fins lucrativos',
	'5': 'Privada sem fins lucrativos',
	'6': 'Privada - Particular em sentido estrito',
	'7': 'Especial',
	'8': 'Privada comunitria',
	'9': 'Privada confessional'

};

class PaginaIes extends React.Component {
  constructor(props) {
  	super(props);
  	
  	this.state = {
  		ies: null
  	};
  }
  
  componentDidMount() {
  	const coIes = this.props.params.id;
  	fetch(`/api/ies/${coIes}`)
      .then(res => res.json())
      .then(jsonRes => this.setState({ies: jsonRes}));
  }
  
  render() {
  	const ies = this.state.ies;
    if(!ies) {
      return (<div />);
    }
    
    const tipoOrg = ORG_ACADEMICA[ies["TP_ORGANIZACAO_ACADEMICA"]];
    const catAdmin = CATEGORIA_ADMIN[ies["TP_CATEGORIA_ADMINISTRATIVA"]]
    return (
    	<div>
    		{/* Header */}
    		<Box
    		 direction="column"
    		 sx={{
    	  		width: '100vw',
    	  		height: 'auto',
    	  		backgroundColor: 'primary.dark',
    	  		color: '#fff',
    	  	 }}
    		>
    			<Chip
    			 icon={<ArrowBackIcon />}
  				 label="Retornar"
  				 component="a"
  				 variant="outlined"
  				 clickable
  				 sx={{
  					backgroundColor: '#fff',
  					fontWeight: '500',
  					fontSize: '1.5em',
  					'&:hover': {
  						backgroundColor: '#ebebeb !important',
  					}
  				 }}
  				 onClick={() => {
                    this.props.navigate(-1);
                 }}
				/>
    			<Typography
    			 variant="h1"
    			 tabIndex="0"
    			 align="center"
    			 sx={{
  					fontSize: '4rem'
  				 }}
    			>
    				{`${ies["NO_IES"]} (${ies["SG_IES"]})`}
    			</Typography>
    		</Box>
    		
    		{/* Grid do contedo */}
    		<Grid
    		 container
    		 columnSpacing={2}
    		 rowSpacing={1}
    		>
    			{/* Informaes da biblioteca */}
    			<Grid item xs={12} md={6}>
    			  <section className="section-container">
    			  	<Typography
    			  	 variant="h2"
    			  	 className="section-header">
    			  		Informaes da biblioteca
    			  	</Typography>
    			  	<Divider className="divider" />
    			  	<Typography>
                    	Quantidade de livros eletrnicos:{" "}
                    	{ies["QT_LIVRO_ELETRONICO"]}
                  	</Typography>
                  	<Typography>
                    	Quantidade de peridicos eletrnicos:{" "}
                    	{ies["QT_PERIODICO_ELETRONICO"]}
                  	</Typography>
                  	<Typography>
                    	Tem acesso ao portal Capes de peridicos?{" "}
                    	{ies["IN_ACESSO_PORTAL_CAPES"] === "0" ? "No" : "Sim"}
                  	</Typography>
                  	<Typography>
                    	Tem acesso a outras bases de dados licenciadas ou compradas?{" "}
                    	{ies["IN_ACESSO_OUTRAS_BASES"] === "0" ? "No" : "Sim"}
                  	</Typography>
                  	<Typography>
                    	Assina outras bases de dados licenciadas ou compradas?{" "}
                    	{ies["IN_ASSINA_OUTRA_BASE"] === "0" ? "No" : "Sim"}
                  	</Typography>
                  	<Typography>
                    	Tem catlogo online?{" "}
                    	{ies["IN_CATALOGO_ONLINE"] === "0" ? "No" : "Sim"}
                  	</Typography>
                  	<Typography>
                    	Oferece servios pela internet?{" "}
                    	{ies["IN_SERVICO_INTERNET"] === "0" ? "No" : "Sim"}
                  	</Typography>
                  	<Typography>
                    	Busca integrada?{" "}
                    	{ies["IN_BUSCA_INTEGRADA"] === "0" ? "No" : "Sim"}
                  	</Typography>
                  	<Typography>
                    	Biblioteca participa de redes sociais?{" "}
                    	{ies["IN_PARTICIPA_REDE_SOCIAL"] === "0" ? "No" : "Sim"}
                  	</Typography>
                  </section>
    			</Grid>
    			
    			<Grid
    			 container
    			 item
    			 xs={12}
    			 md={6}
    			 rowSpacing={2}
    			>
    				{/* Informaes administrativas */}
    				<Grid item xs={12} md={12}>
    					<section className="section-container">
    						<Typography
    			  	 		 variant="h2"
    			  	 		 className="section-header">
    			  				Informaes da instituio
    			  			</Typography>
    			  			<Divider className="divider" />
    						<Typography>Ano do Censo: {ies["NU_ANO_CENSO"]}</Typography>
    						<Typography>Cdigo da instituio: {ies["CO_IES"]}</Typography>
    						<Typography>Mantenedora: {ies["NO_MANTENEDORA"]}</Typography>
    						<Typography>Tipo da Organizao Acadmica: {tipoOrg}</Typography>
    						<Typography>Tipo da Categoria Administrativa: {catAdmin}</Typography>
    					</section>
    				</Grid>
    				
    				{/* Informaes geogrficas */}
    				<Grid item xs={12} md={12}>
    					<section className="section-container">
    						<Typography
    			  	 		 variant="h2"
    			  	 		 className="section-header">
    			  				Informaes geogrficas
    			  			</Typography>
    			  			<Divider className="divider" />
    						<Typography>Regio: {ies["NO_REGIAO_IES"]}</Typography>
    						<Typography>Mesorregio: {ies["NO_MESORREGIAO_IES"]}</Typography>
    						<Typography>Microrregio: {ies["NO_MICRORREGIAO_IES"]}</Typography>
    						<Typography>Estado: {ies["NO_UF_IES"]}</Typography>
    						<Typography>Municpio: {ies["NO_MUNICIPIO_IES"]}</Typography>
    						<Typography>Endereo: {ies["end_completo_y"]}</Typography>
    					
    						<div className="mapouter">
  								<div className="gmap_canvas">
    								<iframe
    								title="mapa"
    							 	width="600"
    							 	height="500"
    							 	id="mapaIframe"
    							 	src={`https://maps.google.com/maps?q=${ies['end_completo_y']}&t=&z=13&ie=UTF8&iwloc=&output=embed`}
    							 	frameBorder="0"
    							 	scrolling="no"
    							 	marginHeight="0"
    							 	marginWidth="0"
    								>
    								</iframe>
  								</div>
							</div>

    					</section>
    				</Grid>
    			</Grid>
    		</Grid>
        </div>
    );
  }
}

// Wrapper para que a classe possa acessar informao de hooks
const withParams = (Component) => (props) => {
	const params = useParams();
	const navigate = useNavigate();
	return (
    	<PaginaIes
        	{...props}
        	params={params}
        	navigate={navigate}
    	/>
    );
};

export default withParams(PaginaIes);